import React from 'react'
import s from './Work.module.scss'
import {Button} from '../../common/styles/components/Button/Button'

export function Work({projectName, description, style, ...props}) {
   return (
      <div className={s.workBlock}>
         <div className={s.projectImg} style={style}>
            <Button name={'Watch'} className={s.workBtn}/>
         </div>
         <div className={s.description}>
            <h4>{projectName}</h4>
            <p>{description}</p>
         </div>
      </div>
   )
}
