import React from 'react'
import s from './Main.module.css'
import sContainer from './../common/styles/Container.module.css'
import bgPhoto from '../assets/image/header.jpg';
import {Button} from '../common/styles/components/Button/Button';

export function Main() {
   const mainGbPhoto = {
      backgroundImage: `url(${bgPhoto})`,
   }

   return (
      <div className={s.mainBlock}>
         <div className={sContainer.container}>
            <div className={s.greeting}>
               {/*<span>Hi,there</span>*/}
               <h1>Hello, I am Sergey</h1>
               <p className={s.about}>I'm web designer and coder.</p>
               <Button name={'SEE MY PORTFOLIO'} className={s.mainBtn}/>
            </div>
            <div className={s.photo}></div>
         </div>
      </div>
   )
}
