import React from 'react'
import s from './MyWorks.module.scss'
import sContainer from './../common/styles/Container.module.css'
import {Work} from './Work/Work'
import {Title} from '../common/styles/components/Title/Title';
import todoImg from '../assets/image/todo-list.png';
import socialNetworkImg from '../assets/image/social-network.jpg';

export function MyWorks() {
   const socialNetwork = {
      backgroundImage: `url(${socialNetworkImg})`,
   };
   const todoList = {
      backgroundImage: `url(${todoImg})`,
   };

   return (
      <div className={s.worksBlock}>
         <div className={`${sContainer.container} ${s.worksContainer}`}>
            <Title title={'My Works'}/>
            <div className={s.works}>
               <Work
                  style={socialNetwork}
                  projectName={'Social Network'}
                  description={
                     'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Accusamus corporis expedita iusto libero molestias odio perferendis quae qui quis veritatis!'
                  }
               />
               <Work
                  style={todoList}
                  projectName={'Todo List'}
                  description={
                     'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Accusamus corporis expedita iusto libero molestias odio perferendis quae qui quis veritatis! Architecto atque, autem consectetur culpa debitis, delectus ducimus eius eos esse est explicabo iure iusto labore mollitia nam nisi perspiciatis placeat praesentium quas rem sint suscipit'
                  }
               />
            </div>
         </div>
      </div>
   )
}
